<template>
    <div id="postList">
        <p class="title_main"></p>
        <div class="view_main">
         <!--   <el-select v-model="typeValue1" placeholder="">
                <el-option
                v-for="item in optionsType"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            <el-select v-model="isUse" placeholder="">
                <el-option
                v-for="item in isUseMenu"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            <el-select v-model="dateVaule" placeholder="">
                <el-option
                v-for="item in dateMenu"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
            <el-input
            placeholder=""
            class="search_input"
            >
            </el-input>
            <el-button></el-button>
            <el-button></el-button>-->
        </div>
        <div class="view_main">
            <!--<el-button @click="addEvent"></el-button>-->
            <!--<ul class="sort_type">
                <li></li>
                <li></li>
                <li></li>
            </ul>-->
        </div>
        <div class="view_main">
            <template>
                <el-table
                :data="postList"
                border
                style="width: 100%">
                    <el-table-column
                    fixed
                    prop="id"
                    label="ID"
                    >
                    </el-table-column>
              <!--      <el-table-column
                    prop="accountNick"
                    label=""
                    >
                    </el-table-column>
                    <el-table-column
                    prop="category"
                    label=""
                    >
                    </el-table-column>-->
                    <el-table-column
                    prop="content"
                    label=""
                    >
                        <template slot-scope="scope">
                            <p v-text="ellipsisEvent(scope.row.content)"></p>
                        </template>
                    </el-table-column>
                    <el-table-column
                    label=""
                    >
                        <template slot-scope="scope">
                            <p v-if="scope.row.imgs"></p>
                            <p v-else></p>
                          <!--  <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
                            <el-button type="text" size="small"></el-button>-->
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="like"
                    label=""
                    >
                    </el-table-column>
                    <el-table-column
                    prop="reply"
                    label=""
                    >
                    </el-table-column>
                    <el-table-column
                    label=""
                    >
                        <template slot-scope="scope">
                            <el-button @click="seeEvent(scope.row.id)" type="text" size="small"></el-button>
                            <el-button type="text" size="small" @click="deleteEvent(scope.row.id)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </template>
        </div>
        <div class="view_main page_main">
            <el-pagination
            background
            layout="prev, pager, next"
            :current-page.sync="nowPage"
            :total="allPage">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                optionsType: [
                    {
                        value: '1',
                        label: ''
                    },
                    {
                        value: '2',
                        label: ''
                    },
                    {
                        value: '3',
                        label: ''
                    },
                    {
                        value: '4',
                        label: ''
                    },
                    {
                        value: '5',
                        label: ''
                    }
                ],
                typeValue1: '',
                isUseMenu: [
                    {value: "true",
                        label: ""},
                    {value: "false",
                        label: ""}
                ],
                isUse: "false",
                tableData: [{
                    id: '2016-05-03',
                    name: '',
                    province: '',
                    city: '',
                    address: ' 1518 ',
                    zip: 200333
                }],
                dateMenu:[
                    {value: "1",
                        label: ""},
                    {value: "7",
                        label: ""},
                    {value: "30",
                        label: ""},
                    {value: "90",
                        label: ""}
                ],
                dateVaule:'',
                userinfo:"",
                postList:[],
                nowPage:1,
                allPage:0
            }
    },
    methods:{
        ellipsisEvent(text){
            console.log(11111111);
            console.log(this.common.ellipsisEvent(text))
            return this.common.ellipsisEvent(text);
        },
        addEvent(){
            this.$router.push("/addPost");
        },
        postDatas(){
            var self = this;
            this.common.getEventToken(this.api.host+this.api.postList+"?page="+this.nowPage+"&per_page=10",{},this.userinfo,function(data){
                console.log(data);
                self.postList = data.data;
                self.allPage = data.last_page * 10;
            })
        },
        seeEvent(id){
            this.$router.push({path:"/editPost",query:{id:id}});
        },
        deleteEvent(id){
            var self = this;
//            this.$confirm('?', '', {
//                confirmButtonText: '',
//                cancelButtonText: '',
//                type: 'warning'
//            }).then(() => {
//            this.common.deleteEventToken(this.api.host+this.api.postList+'/'+id,{},this.userinfo,function(data){
//                console.log(data);
//                self.$message({
//                    type: 'success',
//                    message: '!'
//                    });
//                    self.postDatas();
//                })
//            }).catch(() => {
//                this.$message({
//                    type: 'info',
//                    message: ''
//                });
//        });
            this.common.deleteEventToken(this.api.host+this.api.postList+"/"+id,{},this.userinfo,(data)=>{
                self.$message({
                    type: 'success',
                    message: '!'
                });
                self.postDatas();
            })
    }
    },
    watch:{
        nowPage(){
            this.postDatas();
        }
    },
    mounted:function(){
        this.userinfo = {"token":this.common.cookie.get("token"),"user_id":this.common.cookie.get("user_id")};
        this.postDatas();
    }
    }
</script>
<style>
.view_main{
    overflow:hidden;
    margin-top:10px
    }
.search_input{
    width: 300px;
    }
    .sort_type{
    float:right;
    overflow:hidden;
    }
    .sort_type li{
        display:inline-block;
        margin-left:10px;

    }
    .page_main{
        text-align:center;
    }
</style>


